___CSS_LOADER_EXPORT___.push([module.id, "\n.margin-left-10[data-v-d2bc9f68]{\n        margin-left: 10px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/main/applicationExport.vue"],"names":[],"mappings":";AAqKI;QACI,iBAAiB;AACrB","sourcesContent":["<script setup lang=\"ts\">\nimport { ExportType } from \"@stechquick/algae/lib/qCloudTemp/exporter\";\nimport { ref } from \"vue\";\nimport { useRoute } from \"vue-router\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IExportViewItem } from \"../../../../domain/model/models\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { ICreateExportJob } from \"../../../../domain/useCase/ICreateExportJob\";\nimport { IGetExportSelectionData } from \"../../../../domain/useCase/IGetExportSelectionData\";\nimport { IGetExportTypes } from \"../../../../domain/useCase/IGetExportTypes\";\nimport { IListModelVersions } from \"../../../../domain/useCase/IListModelVersions\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport ExportTypeSelector from \"../../components/export/exportTypeSelector.vue\";\n\nconst router = container.resolve<IRouter>(IRouter);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n\nconst routerParams = router.getRouteParams<{ appID: string }>();\n\nconst exportTypes = ref([] as unknown as Array<{ name: ExportType; text: string; }>);\nconst selectedTypeName = ref(\"\" as unknown as ExportType);\nconst step = ref(\"\" as unknown as \"step1\" | \"step2\");\nconst selectionData = ref([] as unknown as Array<IExportViewItem>);\nconst selectedModels = ref([] as unknown as Array<string>);\nstep.value = \"step1\"\n\nexecutor.execute(async () => {\n    exportTypes.value = await executor.executeUseCase<IGetExportTypes>(IGetExportTypes, { objectType: \"application\" });\n}, { loading: true });\n\nfunction onTypeSelect(name: ExportType) {\n    selectedTypeName.value = name;\n}\n\nasync function onModelChecked(model: IExportViewItem) {\n    executor.execute(async () => {\n        model.isSelected = selectedModels.value.indexOf(model.id) > -1;\n        const versions = [{text:'1.0', value:'1.0'},{text:'2.0', value:'2.0'},{text:'3.0', value:'3.0'}]\n        // const versions = await executor.executeUseCase<IListModelVersions>(IListModelVersions, { modelID: model.id, versionType: \"major\" });\n        model.versions = versions;\n    }, { loading: true });\n}\n\nfunction back() {\n    switch (step.value) {\n        case \"step1\": break; //TODO\n        case \"step2\": step.value = \"step1\"; break;\n    }\n}\n\nfunction next() {\n    step.value = \"step2\";\n    executor.execute(async () => {\n        const applicationID = routerParams.appID;\n        const exportType = selectedTypeName.value;\n        selectionData.value = await executor.executeUseCase<IGetExportSelectionData>(IGetExportSelectionData, { applicationID, exportType });\n    }, { loading: true });\n}\n\nfunction create() {\n    // executor.execute(async () => {\n    //     await executor.executeUseCase<ICreateExportJob>(ICreateExportJob, {\n    //         app: { ID: routerParams.appID, name: \"aaa\" },\n    //         exportType: selectedTypeName.value,\n    //         selectionData: selectionData.value\n    //     });\n    // }, { loading: true });\n}\n\n</script>\n\n<template>\n    <h2>Export Application</h2>\n\n    <!-- BUTTONS (HEADER) -->\n    <!-- <div>\n        <button @click=\"back()\">Back</button>\n        <button v-if=\"step == 'step1'\" :disabled=\"!selectedTypeName\" @click=\"next()\">Next</button>\n        <button\n            v-if=\"step == 'step2'\"\n            :disabled=\"selectedModels.length == 0\"\n            @click=\"create()\"\n        >Create</button>\n    </div> -->\n\n    <div>\n        <p></p>\n    </div>\n\n    <!-- STEP 1 -->\n    <div v-show=\"step == 'step1'\">\n        <b>Please select an export type</b>\n        <div\n            v-for=\"exportType in exportTypes\"\n            :key=\"exportType.name\"\n            v-bind:value=\"exportType.name\"\n        >\n            <label>\n                <input\n                    type=\"radio\"\n                    name=\"radio\"\n                    :value=\"exportType.name\"\n                    :id=\"exportType.name\"\n                    @change=\"onTypeSelect(exportType.name)\"\n                />\n                {{ exportType.text }}\n            </label>\n        </div>\n    </div>\n\n    <!-- STEP 2 -->\n    <div v-if=\"step == 'step2'\">\n        <b>Please select the models in the modules which you need to export</b>\n        <div v-for=\"module in selectionData\" :key=\"module.id\" v-bind:value=\"module.id\">\n            <div>\n                <p></p>\n            </div>\n            <b>{{ module.name }}</b>\n            <div v-for=\"model in module.children\" :key=\"model.id\" v-bind:value=\"model.id\">\n                <div class=\"margin-left-10\">\n                    <input\n                        type=\"checkbox\"\n                        :id=\"model.id\"\n                        :value=\"model.id\"\n                        v-model=\"selectedModels\"\n                        :key=\"model.id\"\n                        @change=\"onModelChecked(model)\"\n                    />\n                    {{ model.name }}\n                    <select\n                        :disabled=\"selectedModels.indexOf(model.id) <= -1\"\n                        v-model=\"model.selectedVersion\"\n                    >\n                        <option disabled value>Please select one</option>\n                        <option\n                            v-for=\"version in model.versions!\"\n                            :key=\"version.value\"\n                        >{{ version.text }}</option>\n                    </select>\n                </div>\n            </div>\n            <div class=\"margin-left-10\" v-if=\"!module.children || module.children.length == 0\">\n                There isn't any item in this module\n            </div>\n        </div>\n    </div>\n\n    <div>\n        <p></p>\n    </div>\n\n    <!-- BUTTONS (FOOTER) -->\n    <div>\n        <button @click=\"back()\">Back</button>\n        <button v-if=\"step == 'step1'\" :disabled=\"!selectedTypeName\" @click=\"next()\">Next</button>\n        <button\n            v-if=\"step == 'step2'\"\n            :disabled=\"selectedModels.length == 0\"\n            @click=\"create()\"\n        >Create</button>\n    </div>\n</template>\n\n<style scoped>\n    .margin-left-10{\n        margin-left: 10px;\n    }\n</style>"],"sourceRoot":""}]);
